header {
  background-color: #141414;
  padding: 29px 20px 0;
}

.nav-menu-bar {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo-full {
  align-items: center;
  gap: 4px;
  display: flex;
}

.logo-svg {
  width: 14px;
  height: 14px;
  fill: #fff;
}

.logo-text {
  width: 80px;
  height: 10px;
  letter-spacing: -.03em;
  text-align: left;
  color: #fff;
  font-family: Epilogue;
  font-size: 12px;
  font-weight: 400;
  line-height: 12.3px;
}

.open-menu, .close-menu {
  cursor: pointer;
}

.menu-list, .address {
  display: none;
}

@media (min-width: 768px) {
  header {
    padding: 20px 32px;
  }

  .open-menu, .modal-backdrop {
    display: none;
  }

  .menu-list {
    color: #fff;
    gap: 40px;
    margin-left: 80px;
    margin-right: auto;
    display: flex;
  }

  .menu-link {
    text-align: center;
    font-family: Epilogue;
    font-size: 9px;
    font-weight: 400;
    line-height: 12px;
  }

  .address {
    display: initial;
    background-color: #1e2827;
    border-radius: 8px;
    padding: 18px 36px;
  }

  .address-phone {
    display: initial;
    text-align: center;
    color: #fff;
    font-family: Epilogue;
    font-size: 9px;
    font-weight: 400;
    line-height: 12px;
  }
}

@media (min-width: 1440px) {
  header {
    padding: 20px 132px;
  }

  .logo-svg {
    width: 19px;
    height: 19px;
  }

  .logo-text {
    font-size: 15px;
  }
}

